  width: 40vw;
  margin-left: 0;
  margin-top: 100px;
}

.card-food {
  width: 200px; /* Ajusta el ancho según tus necesidades */
  margin-bottom: 20px; /* Ajusta el espaciado entre las tarjetas */
  border: 1px solid #ccc; /* Agrega un borde opcional */
  padding: 10px; /* Ajusta el espacio interno de la tarjeta */
  height: 500px;
}

.img-food{
  width: 150px;
  /* margin-left: calc(50% - 75px); */
  height: 150px;
}




