}


button{
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Nunito', sans-serif;
}

.primary-button {
  background-color: #ffcc00; /* Amarillo McDonald's */
  color: #d21f17; /* Rojo McDonald's */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Sombreado */
  font-size: 20px;
  padding: 2.5px;
  font-family: 'Nunito', sans-serif;
}

/* Estilo para botón secundario */
.secondary-button {
  background-color: #d21f17; /* Rojo McDonald's */
  color: #fff; /* Blanco */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Sombreado */
  padding: 2.5px;
  font-family: 'Nunito', sans-serif;
}

button:hover{
  opacity: 0.9; /* Reducir opacidad al pasar el ratón */
  transform: translateY(-1px); 
}

